$range: U+00-024F, U+3000-303F, U+30-39; //   
// ====== Jost ======//
/* latin-ext */
@font-face {
  font-family: 'Jost';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/jost/v4/92zatBhPNqw73ord4jQmfxIC7w.woff2) format('woff2');
  unicode-range: $range;
}
